
@mixin bg-blur($bgColor: rgba($color-bg, 0.7), $blur: $gap) {

    @supports (-webkit-backdrop-filter: blur(1px)) or (backdrop-filter: blur(1px)) {
        @if (#{$bgColor} != "") {
            background-color: #{$bgColor};
        }
        backdrop-filter: blur($blur);
    }
}




